import { Component, OnInit, ViewChild } from '@angular/core';
import { DataTableDirective } from 'angular-datatables';
import { Subject } from 'rxjs';
import { ITADSupliers } from '../../_models/ITADSupliers';
import { AlertifyService } from '../../_services/alertify.service';
import { LoadspecsService } from '../../_services/loadspecs.service';

@Component({
  selector: 'app-itda-suppliers',
  templateUrl: './itda-suppliers.component.html',
  styleUrls: ['./itda-suppliers.component.css']
})
export class ItdaSuppliersComponent implements OnInit {
  users: ITADSupliers[];
  @ViewChild(DataTableDirective) dtElement: DataTableDirective;
  dtOptions: any = {
    pagingType: 'full_numbers',
    pageLength: 25,
    retrieve: true,
    order: [[0, 'desc']]
  };
dtInstance: DataTables.Api;
isDtInitialized: boolean = false;
dtTrigger: Subject<ITADSupliers> = new Subject();

  constructor(private loadspecsService: LoadspecsService, private alertify: AlertifyService) { }

  ngOnInit(): void {
    this.loadITADSup();
  }
  loadITADSup() {
    return this.loadspecsService.getITADSuppliers().subscribe((users: ITADSupliers[]) => {
      this.users = users;
      console.log(this.users);
      if (this.isDtInitialized) {
        this.dtElement.dtInstance.then((dtInstance: DataTables.Api) => {
          dtInstance.destroy();
          this.dtTrigger.next();
        });
      } else {
        this.isDtInitialized = true;
        this.dtTrigger.next();
      }
    }, error => {
      this.alertify.error(error);
    });
  }

}
